/*

light-red: #F5717C
red: #EA2333
dark-red: #AE0E1A

*/
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  color: #777;
  padding: 30px;
}

.header-background {
  background-color: #57423F;
}

.header-shadow {
  /* filter: drop-shadow(-1px 6px 3px rgba(50, 50, 0, 0.5)); */
  filter: drop-shadow(-1px 5px 10px rgba(0,0,0,0.45));
}

.header {
  height: 95vh;
  background-image: linear-gradient(
    to right bottom, 
    #f5717ccc, 
    rgba(244, 101, 111, 0.8), 
    rgba(243, 88, 98, 0.8), 
    rgba(241, 75, 84, 0.8), 
    rgba(238, 60, 70, 0.8), 
    rgba(232, 51, 61, 0.8), 
    rgba(225, 41, 53, 0.8), 
    rgba(219, 30, 44, 0.8), 
    rgba(208, 26, 39, 0.8), 
    rgba(196, 22, 35, 0.8), 
    rgba(185, 18, 30, 0.8), 
    rgba(175, 14, 25, 0.8)),
  url(./media/pexels-daniela-echavez-1120970.jpg);
  background-size: cover;
  background-position: bottom;
  position: relative;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 75vh);
}

.logo-box {
  position: absolute;
  top: 15px;
  left: 15px;
}

.logo {
  width: 150px;
}

.text-box {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.heading-primary {
  color: #FFF;
  text-transform: uppercase;
  margin-bottom: 60px;
}

.heading-primary-main {
  display: block;
  font-size: 45px;
  font-weight: 400;
  letter-spacing: 20px;
  animation: moveInLeft 1s ease-out;
}

.heading-primary-sub {
  display: block;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 15px;
  animation: moveInRight 1s ease-out;
}

.btn:link,
.btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  padding: 13px 40px;
  display: inline-block;
  border-radius: 100px;
  transition: all .2s;
  position: relative;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0,0,0,.2);
}

.btn:active {
  transform: translateY(-1px);
  box-shadow: 0 5px 10px rgba(0,0,0,.2);
}

.btn-white {
  background-color: #FFF;
  color: #777;
}

.btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all .4s;
}

.btn-white::after {
  background-color: #FFF;
}

.btn:hover::after {
  transform: scale(1.4);
  opacity: 0;
}

.btn-animated {
  animation: moveInBottom .5s ease-out .75s;
  animation-fill-mode: backwards;
}

@keyframes moveInRight {
    0% {
      opacity: 0;
      transform: translateX(100px);
    }
  
    80% {
      transform: translateX(-10px);
    }
  
    100% {
      opacity: 1;
      transform: translate(0);
    }
  }

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }

  80% {
    transform: translateX(10px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

