  border-radius: 8px;
  cursor: pointer;
  box-shadow: 10px 10px 16px -5px rgba(0,0,0,0.55);
  animation: fadeIn .3s ease-in;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
