.gm-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gm-shadow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45);
  z-index: 1;
}

.gm-container {
  width: 1000px;
  height: 640px;
  z-index: 2;
  box-shadow: 10px 15px 21px -5px rgba(0,0,0,0.75);
}

.slide-box {
  position: relative;
  width: 1000px;
  min-height: 640px;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slide-img {
  /* width: 100%; */
  height: 100%;
  object-fit: cover;
}

.prev {
  position: absolute;
  left: 0;
  height: 100%;
  width: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .2s;
}

.prev:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.35);  
}

.next {
  position: absolute;
  right: 0;
  height: 100%;
  width: 70px;  
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .2s;
}

.next:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.35);
}